export default {
	data() {
		return {
			title: '分享有礼',
			shareText: '分享小程序，获得时长奖励，邀请你一起体验！',
			href:"https://uniapp.dcloud.io",
			imageUrl: 'http://allchain.oss-cn-shanghai.aliyuncs.com/uploads/20190521/f57ebce8a72b823912904fe76eda0909.png',
			image: 'http://allchain.oss-cn-shanghai.aliyuncs.com/uploads/20190521/f57ebce8a72b823912904fe76eda0909.png',
			shareType:1,
			providerList: []
		}
	},
	computed:{
		isDisableButton() {
			return function(item) {
				if(this.shareType === 0 && item.id === 'qq'){
					return true;
				}
				if(this.shareType === 5 && item.name !== '分享到微信好友'){
					return true;
				}
				return false;
			}
		}
	},
	onLoad: function () {
		uni.getProvider({
			service: 'share',
			success: (e) => {
				console.log('success', e);
				let data = []
				for (let i = 0; i < e.provider.length; i++) {
					switch (e.provider[i]) {
						case 'weixin':
							data.push({
								name: '分享到微信好友',
								id: 'weixin',
								sort:0
							})
							data.push({
								name: '分享到微信朋友圈',
								id: 'weixin',
								type:'WXSenceTimeline',
								sort:1
							})
							break;
						case 'sinaweibo':
							data.push({
								name: '分享到新浪微博',
								id: 'sinaweibo',
								sort:2
							})
							break;
						case 'qq':
							data.push({
								name: '分享到QQ',
								id: 'qq',
								sort:3
							})
							break;
						default:
							break;
					}
				}
				this.providerList = data.sort((x,y) => {
					return x.sort - y.sort
				});
			},
			fail: (e) => {
				console.log('获取分享通道失败', e);
				uni.showModal({
					content:'获取分享通道失败',
					showCancel:false
				})
			}
		});
	},
	methods: {
		async share(e) {
			console.log('分享通道:'+ e.id +'； 分享类型:' + this.shareType);
			
			if(!this.shareText && (this.shareType === 1 || this.shareType === 0)){
				uni.showModal({
					content:'分享内容不能为空',
					showCancel:false
				})
				return;
			}
			
			if(!this.image && (this.shareType === 2 || this.shareType === 0)){
				uni.showModal({
					content:'分享图片不能为空',
					showCancel:false
				})
				return;
			}
			
			let shareOPtions = {
				provider: e.id,
				scene: e.type && e.type === 'WXSenceTimeline' ? 'WXSenceTimeline' : 'WXSceneSession', //WXSceneSession”分享到聊天界面，“WXSenceTimeline”分享到朋友圈，“WXSceneFavorite”分享到微信收藏     
				type: this.shareType,
				imageUrl: 'http://allchain.oss-cn-shanghai.aliyuncs.com/uploads/20190521/f57ebce8a72b823912904fe76eda0909.png',
				success: (e) => {
					console.log('success', e);
					uni.showModal({
						content: '已分享',
						showCancel:false
					})
				},
				fail: (e) => {
					console.log('fail', e)
					uni.showModal({
						content: e.errMsg,
						showCancel:false
					})
				},
				complete:function(){
					console.log('分享操作结束!')
				}
			};
			console.log(shareOPtions);
			uni.share(shareOPtions);
		},
		radioChange(e){
			console.log('type:' + e.detail.value);
			this.shareType = parseInt(e.detail.value);
		},
	}
}
